body {
  font-family: 'IM Fell Great Primer', serif;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 10s linear;
          animation: App-logo-spin infinite 10s linear;
  height: 80px;
}

.App-header {
  height: 200px;
  padding: 20px;
  color: white;
  background: rgb(0, 7, 117); /*For browsers that do not support gradients*/
  background: -webkit-linear-gradient(left, black, rgb(32, 62, 149) , black); /* For Safari 5.1 to 6.0 */
  background: -o-linear-gradient(black, rgb(32, 62, 149) , black); /* For Opera 11.1 to 12.0 */ /* For Firefox 3.6 to 15 */
  background: -webkit-gradient(linear, left top, right top, from(black), color-stop(rgb(32, 62, 149)) , to(black));
  background: -o-linear-gradient(left, black, rgb(32, 62, 149) , black);
  background: linear-gradient(to right, black, rgb(32, 62, 149) , black); /* Standard syntax */
}

.App-intro {
  font-size: large;
}

.prediction-ball{
  margin-top: 30px;

}

input:focus{
    outline: none;

}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.fortune {
  color: white;
  position: absolute;
  /*margin: 50%;*/
  /*white-space: nowrap;*/
  z-index: 2;
  max-width: 80px;
}

.ballAndFortune {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}



